.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.btn {
  display: inline-block;
  vertical-align: top; }

.btn-new {
  padding: 8px 18px;
  background: #43ccef;
  /* Old browsers */
  background: -moz-linear-gradient(left, #43ccef 1%, #48c2ea 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #43ccef 1%, #48c2ea 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #43ccef 1%, #48c2ea 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#43ccef', endColorstr='#48c2ea',GradientType=1 );
  /* IE6-9 */
  border-radius: 0 5px 5px 0;
  color: #fff;
  font-size: 11px; }
  .btn-new:hover {
