  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #e8d143 0%, #f4e777 0%, #f4e777 0%, #e8d143 13%, #f4e777 34%, #e8d143 50%, #fcf399 70%, #d9c32e 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e8d143', endColorstr='#d9c32e',GradientType=1 );
